<template>
  <div class="v-activity-list clearfix">
    <ol>
      <li class="clearfix">
        <div class="v-activity-content v-name">
          
        </div>
        <div class="v-activity-content v-time">
          
        </div>
        <div class="v-activity-content v-type">
          
        </div>
        <div class="v-activity-content v-operation">
          
        </div>
      </li>
      <template v-if="tableList.length > 0">
        <li class="clearfix" v-for="itemData in tableList" :key="itemData.behavior_id">
          <div class="v-activity-content v-name">
            {{itemData.activity_name}}
          </div>
          <div class="v-activity-content v-time">
            {{itemData.generated_at}}
          </div>
          <div class="v-activity-content v-type">
            {{itemData.type}}
          </div>
          <div class="v-activity-content v-operation">
            <a :href="PUBLIC_PATH+'subscribe/'+itemData.behavior_id"></a>
          </div>
        </li>
      </template>
      <li class="v-nodata" v-else>
        <div>
          
        </div>
      </li>
      <!-- <li class="clearfix">
        <div class="v-activity-content v-name">
          
        </div>
        <div class="v-activity-content v-time">
          2018-07-24 12:24:23
        </div>
        <div class="v-activity-content v-type">
          
        </div>
        <div class="v-activity-content v-operation">
          <a :href="PUBLIC_PATH+'subscribe/666'"></a>
        </div>
      </li> -->
    </ol>
    <div class="pagination-box">
      <div class="page-pagination"
           v-if="total>pageSize">
        <ve-pagination :total="total"
                       :pageSize="pageSize"
                       :currentPage="searchParams.page"
                       @changePage="changePage" />
      </div>
    </div>
  </div>
</template>
<script>
// import { MessageBox } from 'components/common/message-box'
import VePagination from 'src/components/ve-pagination'
import userService from 'src/api/user-service'
export default {
  components: { VePagination },
  data () {
    return {
      tableList: [],
      PUBLIC_PATH: process.env.PUBLIC_PATH,
      searchParams: {
        business_consumer_uid: 0,
        page: 1,
        page_size: 9
      },
      total: 0,
      pageSize: 9
    }
  },
  created () {
    this.getDataList()
  },
  computed: {
  },
  watch: {
  },
  methods: {
    getDataList () {
      this.searchParams.business_consumer_uid = this.$route.params.id
      this.$config({ handlers: true }).$get(userService.GET_SURVER_LIST, this.searchParams).then((res) => {
        if (res.code === 200) { // 
          res.data.list.map((item, indx) => {
            if (item.imgUrl) {
              item.imgUrl = this.$imgHost + '/' + item.imgUrl
            } else {
              item.imgUrl = '//cnstatic01.e.vhall.com/static/img/v35-webinar.png'
            }
            return item
          })
          this.tableList = res.data.list
          this.total = res.data.total
          this.searchParams.page = parseInt(res.data.currPage)
        }
      }).catch(err => {
        if (err.code !== 201) {
          this.$messageBox({
            header: '',
            content: err.msg,
            confirmText: '',
            handleClick: (e) => {
              if (e.action === 'cancel') {
              } else if (e.action === 'confirm') {
              }
            }
          })
        }
      })
      // this.$get(activityService.GET_ACTIVITY_LIST, this.searchParams).then((res) => {
      //   res.data.list.map((item, indx) => {
      //     if (item.imgUrl) {
      //       item.imgUrl = this.$imgHost + '/' + item.imgUrl
      //     } else {
      //       item.imgUrl = '//cnstatic01.e.vhall.com/static/img/v35-webinar.png'
      //     }
      //     return item
      //   })
      //   res.data.list.forEach(element => {
      //     this.tableList.push(element)
      //   })
      //   this.total = res.data.total
      //   this.searchParams.page = parseInt(res.data.currPage) + 1
      //   this.searchParams.totalPage = res.data.totalPage
      // })
    },
    changePage (currentPage) {
      this.searchParams.page = currentPage
      this.getDataList()
    }
  }
}
</script>
<style lang="scss" scoped>
.v-activity-list /deep/ {
  width: 100%;
  position: relative;
  margin: 0 auto;
  width: 100%;
  ol {
    width: 100%;
    li {
      width: 100%;
      height: 52px;
      line-height: 52px;
      background-color: #fff;
      border-bottom: 1px solid #e2e2e2;
      &:first-child {
        height: 61px;
        line-height: 61px;
        background-color: #f5f5f5;
        &:hover {
          background-color: #f5f5f5;
        }
      }
      &:hover {
        background-color: #f0f1fe;
      }
      &.v-nodata {
        text-align: center;
        &:hover {
          background-color: #fff;
        }
      }
      .v-activity-content {
        float: left;
        text-align: left;
        padding: 0 20px;
        word-break: break-all;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      .v-name {
        width: 53%;
      }
      .v-time {
        width: 23%;
      }
      .v-type {
        width: 13%;
      }
      .v-operation {
        width: 9%;
        text-align: center;
        padding: 0;
        a {
          &:hover {
            color: #4b5afe;
          }
        }
      }
    }
  }
  .pagination {
    width: 100%;
    margin-top: 20px;
    overflow: hidden;
  }
  .page-pagination {
    margin: 30px 10px 20px 0;
    text-align: center;
  }
  .pagination-box {
    width: 100%;
    margin-top: 20px;
    overflow: hidden;
  }
}
</style>
