<template>
  <div class="industry-wrap">
    <el-select v-model="value"
               @change="change"
               @blur="blur"
               placeholder="">
      <el-option v-for="item in educations"
                 :key="item.value"
                 :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: '',
      educations: [
        {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }
      ]
    }
  },
  methods: {
    change (e) {
      this.$emit('input', e)
      this.$emit('change', e)
    },
    blur () {
      this.$emit('saveInfo', this.value)
    }
  }
}
</script>

