<template>
  <div style="height: 100%;">
    <div class="v-footprints bscroll" ref="bscroll" @scroll="scrollEvent($event)">
      <ol class="bscroll-container">
        <li class="v-footprint" v-for="itemData in dataList" :key="itemData.behavior_id">
          <i class="iconfont icon-dian">
          </i>
          <i class="v-border"></i>
          <p class="v-time">
            {{itemData.generated_at}}
          </p>
          <p class="v-content">
            {{itemData.event==='JOIN_ACTIVITY'?'':''}} {{itemData.data.activity_title?itemData.data.activity_title:''}}
            <button @click="showRecord(itemData.activity_id,itemData.data.activity_title,itemData.generated_at)" v-if="itemData.event!='FIRST_VISIT'">
              
            </button>
          </p>
        </li>
         <!-- <li class="v-footprint">
          <i class="iconfont icon-dian">
          </i>
          <i class="v-border"></i>
          <p class="v-time">
            2018-07-07 18:16:35
          </p>
          <p class="v-content">
             2018
            <button @click="showRecord(66)">
              
            </button>
          </p>
        </li> -->
      </ol>
    </div>
    <message-box v-show="recordBoxShow"
                 @handleClick="recordBoxClick"
                 width="450px"
                 class="message-box v-record"
                 confirmText=""
                 type='prompt'
                 header=''>
      <div>
        <p class="v-explain">
          {{showActivity.name}}
        </p>
        <p class="v-time">
          {{showActivity.time}}
        </p>
        <div class="v-steps-content">
          <div class="v-steps bscroll" ref="infoscroll">
            <ol class="bscroll-container">
              <li class="v-step" v-for="itemData in dataInfoList" :key="itemData.behavior_id">
                <div class="v-content">
                  <i class="iconfont icon-dian">
                  </i>
                  <i class="v-border"></i>
                  <p class="v-content-time">
                    {{itemData.generated_at}}
                  </p>
                  <p class="v-content-title">
                    {{event(itemData.event,itemData.data)}}
                  </p>
                </div>
              </li>
              <!-- <li class="v-step">
                <div class="v-content">
                  <i class="iconfont icon-dian">
                  </i>
                  <i class="v-border"></i>
                  <p class="v-content-time">
                    2017-07-07 09:30:05
                  </p>
                  <p class="v-content-title">
                    
                  </p>
                </div>
              </li> -->
            </ol>
          </div>
        </div>
      </div>
    </message-box>
  </div>
</template>
<script>
import userService from 'src/api/user-service'
import BScroll from 'better-scroll'
export default {
  data () {
    return {
      scrollEvent: null,
      dataList: [],
      dataInfoList: [],
      searchParams: {
        business_consumer_uid: 0,
        type: 1,
        page: 1,
        page_size: 20,
        totalPage: 0
      },
      searchInfoParams: {
        business_consumer_uid: 0,
        type: 2,
        page: 1,
        page_size: 20,
        totalPage: 0
      },
      showActivity: {
        name: '',
        time: ''
      },
      total: 0,
      infoTotal: 0,
      recordBoxShow: false // 
    }
  },
  mounted () {
    this.getDataList()
    // this.initScroll()
  },
  created () {
    this.scrollEvent = this.debounce(e => {
      if (this.$refs.bscroll.offsetHeight + this.$refs.bscroll.scrollTop > this.$refs.bscroll.scrollHeight - 100) {
        if (this.searchParams.page < this.searchParams.totalPage) {
          this.searchParams.page = parseInt(this.searchParams.page) + 1
          this.getDataList()
        }
      }
    }, 50)
  },
  methods: {
    debounce (func, wait, immediate) {
      var timeout
      return function () {
        var context = this
        var args = arguments
        var later = function () {
          timeout = null
          if (!immediate) func.apply(context, args)
        }
        var callNow = immediate && !timeout
        clearTimeout(timeout)
        timeout = setTimeout(later, wait)
        if (callNow) func.apply(context, args)
      }
    },
    showRecord (activityId, name, time) {
      this.recordBoxShow = true
      this.showActivity.name = name
      this.showActivity.time = time
      this.searchInfoParams.page = 1
      this.searchInfoParams.activity_id = activityId
      this.getDataInfoList(false)
      this.initInfoScroll()
    },
    recordBoxClick (e) {
      if (e.action === 'cancel') {
        this.recordBoxShow = false
      }
    },
    getDataList () {
      this.searchParams.business_consumer_uid = this.$route.params.id
      this.$config({ handlers: true }).$get(userService.GET_BEHAVIOR_LIST, this.searchParams).then((res) => {
        if (res.data && res.data.list) {
          res.data.list.forEach(element => {
            this.dataList.push(element)
          })
          this.total = res.data.total
          this.searchParams.page = parseInt(res.data.page) + 1
          this.searchParams.totalPage = Math.ceil(res.data.total / res.data.page_size)
        }
      }).catch(err => {
        if (err.code !== 201) {
          this.$messageBox({
            header: '',
            content: err.msg,
            confirmText: '',
            handleClick: (e) => {
              if (e.action === 'cancel') {
              } else if (e.action === 'confirm') {
              }
            }
          })
        }
      })
    },
    // initScroll () {
    //   let _this = this
    //   this.$nextTick(() => {
    //     let bscrollDom = this.$refs.bscroll
    //     this.aBScroll = new BScroll(bscrollDom, {
    //       'scrollbar': true,
    //       'click': true,
    //       'mouseWheel': true,
    //       'probeType ': 3,
    //       'pullup': true
    //       //  probeType  1 scroll  probeType  2  scroll  probeType  3  momentum  scroll 
    //     })
    //     this.aBScroll.on('scrollEnd', () => {
    //       // 
    //       if (this.aBScroll.y <= (this.aBScroll.maxScrollY)) {
    //         if (_this.searchParams.page <= _this.searchParams.total) {
    //           this.getDataList()
    //         }
    //       }
    //     })
    //   })
    // },
    getDataInfoList (isAdd) {
      this.searchInfoParams.business_consumer_uid = this.$route.params.id
      if (!isAdd) {
        this.dataInfoList = []
      }
      this.$config({ handlers: true }).$get(userService.GET_BEHAVIOR_LIST, this.searchInfoParams).then((res) => {
        res.data.list.forEach(element => {
          this.dataInfoList.push(element)
        })
        this.infoTotal = res.data.total
        this.searchInfoParams.total = res.data.total
        this.searchInfoParams.totalPage = Math.ceil(res.data.total / res.data.page_size)
      }).catch(err => {
        if (err.code !== 201) {
          this.$messageBox({
            header: '',
            content: err.msg,
            confirmText: '',
            handleClick: (e) => {
              if (e.action === 'cancel') {
              } else if (e.action === 'confirm') {
              }
            }
          })
        }
      })
    },
    initInfoScroll () {
      let _this = this
      this.$nextTick(() => {
        let bscrollDom = this.$refs.infoscroll
        this.infoscroll = new BScroll(bscrollDom, {
          'scrollbar': true,
          'click': true,
          'mouseWheel': true,
          'probeType ': 3,
          'pullup': true
          //  probeType  1 scroll  probeType  2  scroll  probeType  3  momentum  scroll 
        })
        this.infoscroll.on('scrollEnd', () => {
          // 
          // console.log(this.infoscroll.y + ',' + this.infoscroll.maxScrollY)
          if (this.infoscroll.y === (this.infoscroll.maxScrollY)) {
            if (_this.searchInfoParams.page < _this.searchInfoParams.totalPage) {
              _this.searchInfoParams.page = parseInt(_this.searchInfoParams.page) + 1
              _this.getDataInfoList(true)
            }
          }
        })
      })
    },
    event (type, data) {
      let strType = ''
      switch (type) {
        case 'JOIN_ACTIVITY_WEBSITE':
          strType = ''
          break
        case 'LEAVE_ACTIVITY_WEBSITE':
          strType = ''
          break
        case 'JOIN_ACTIVITY_GUIDE':
          strType = ''
          break
        case 'LEAVE_ACTIVITY_GUIDE':
          strType = ''
          break
        case 'ORDER_ACTIVITY':
          strType = ''
          break
        case 'APPLY_ACTIVITY':
          strType = ''
          break
        case 'JOIN_ACTIVITY':
          strType = ''
          break
        case 'FIRST_CHAT':
          strType = ''
          break
        case 'FIRST_SHARE':
          strType = ''
          break
        case 'COMMIT_SURVEY':
          strType = ''
          break
        case 'SHARE_COUNT':
          strType = `${data.count}`
          break
        case 'STAY_ACTIVITY_WEBSITE_TIME':
          strType = `${data.time > 60 ? Math.round(data.time / 60) : 1}`
          break
        case 'STAY_ACTIVITY_TIME':
          strType = `${data.time > 60 ? Math.round(data.time / 60) : 1}`
          break
        case 'SEND_CHAT_COUNT':
          strType = `${data.count}`
          break
        case 'INVITE_COUNT':
          strType = `${data.count}`
          break
        case 'CONCERN_WX':
          strType = `${data.wx_name}`
          break
        case 'EMAIL_SUBSCRIBE':
          strType = `${data.business_name}`
          break
      }
      return strType
    }
  }
}
</script>
<style lang='scss' scoped>
.v-footprints {
  .v-footprint {
    .iconfont {
      color: #779fff;
    }
  }
}
.v-record /deep/ {
  .ve-message-box__container {
    max-height: 562px;
    .v-steps-content {
      height: 450px;
      .v-steps {
        position: relative;
        width: 100%;
        overflow: hidden;
        height: calc(100%);
        .iconfont {
          color: #779fff;
        }
      }
    }
  }
}
</style>
